private_cloud.controller('addSubNetworkController',['$scope','$state',function($scope,$state){
    $scope.formData = {
        enable_dhcp:true, //激活DHCP
        network_id:$state.params.id,
        ip_version:4
    };
    $scope.ipVersion = [
        {
            name:'Ipv4',
            value:4
        },
        {
            name:'Ipv6',
            value:6
        }
    ]; 
}]);